.signin-container {
  display: flex;
  width: 100%;
  height: 100%;

  .left,
  .right {
    justify-content: center;
    align-items: center;
    width: 50%;
  }

  .left-content {
    display: flex;
    flex-direction: column;
    width: 60%;
    margin-top: 3%;

    .signin-btn {
      display: flex;
      justify-content: center;
      position: relative;
      cursor: pointer;
      margin-bottom: 1rem;

      img {
        margin-left: -2px;
      }
      .sign-txt {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: white;
        font-weight: 500;
        font-size: 1rem;
      }
    }

    .tooltext {
      text-align: right;
      padding: 1rem 0;
      cursor: pointer;
      font-family: "Poppins";
      font-size: 13px;
    }
    .text {
      font-family: "Poppins";
      font-size: 0.8rem;
      text-align: center;
    }

    .link {
      cursor: pointer;
      color: #2082f4;
    }
  }

  .right {
    background-color: #f4f4ff;
    border-top-left-radius: 2rem;
    border-bottom-left-radius: 2rem;
    height: 100vh;
    display: flex;
    flex-direction: column;

    .full {
      flex: 1;

      img {
        width: 100%;
        align-self: flex-end;
        height: 49vh;
      }
    }

    .slider {
      width: 86%;
      margin: 10% 1%;

      .slide {
        white-space: normal;
        line-height: 3.5rem;

        .description {
          font-family: "Playfair Display", serif;
          color: #211149;
        }
        .author {
          text-align: right;
          font-family: "Poppins";
          color: #706c6c;
          font-size: 0.9rem;
          margin-bottom: 3%;
        }
      }
    }
  }
}
